<template>
  <el-card>
    <el-card style="width: 90%; margin: 10px auto;padding: 0; text-align: center;">
      <AddLinkCard @updateLive="getLive"></AddLinkCard>
    </el-card>
    <el-card style="width: 90%; margin: 0 auto;">
      <template>
        <el-table
            :data="tableData"
            style="text-align: center;margin: 0 auto;"
            border
            :row-class-name="tableRowClassName">
          <el-table-column
              prop="createTime"
              label="创建时间"
              width="180">
          </el-table-column>
          <el-table-column
              prop="liveId"
              label="直播间id"
              width="120">
          </el-table-column>
          <el-table-column
              prop="remark"
              label="备注"
              width="205">
          </el-table-column>
          <el-table-column
              prop="status_c"
              label="状态"
              width="65">
          </el-table-column>
          <el-table-column
              prop="device"
              label="设备码"
              width="265">
          </el-table-column>

          <el-table-column
              label="重置设备码"
              width="120">
            <template slot-scope="scope">

              <el-button
                  @click.native.prevent="stop(scope.$index, tableData)"
                  type="text"
                  size="small">
                重置
              </el-button>

            </template>
          </el-table-column>
          <el-table-column
              label="状态"
              width="120">
            <template slot-scope="scope">

              <el-button
                  @click.native.prevent="edit(scope.$index, tableData)"
                  type="text"
                  v-show="1 "
                  size="small">
                修改
              </el-button>

            </template>
          </el-table-column>
          <el-table-column
              label="删除"
              width="60">
            <template slot-scope="scope">
              <el-button
                  @click.native.prevent="deleteRow(scope.$index, tableData)"
                  type="text"
                  size="small">
                移除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </el-card>
  </el-card>
</template>

<script>

export default {
  name: "LiveView",
  data() {
    return {
      tableData: [],

    }
  },
  components: {},
  beforeMount() {

    this.getLive()
    setInterval(() => {
      this.getLive()

    }, 2000)
  },

  methods: {
    tableRowClassName({rowIndex}) {
      let data = this.tableData[rowIndex];
      if (data['status'] !== "1") {
        data['status_c'] = '已启用'
        return 'success-row';
      } else {
        data['status_c'] = '已关闭'
        return ''
      }

    },
     async edit(index, rows) {
       let data = {
         id: this.tableData[index].id
       }
       await this.$axios.post("/api/live/change", JSON.stringify(data)).then(res => {
         if (String(res.data.code) === '1') {
           this.$message.success(res.data.msg)
         } else {
           this.$message.error(res.data.msg)
         }
       }).catch(err => {
         this.$message.error(err)
       })
       rows.splice(index, 1);
     },
    async deleteRow(index, rows) {
      let data = {
        id: this.tableData[index].id
      }
      await this.$axios.post("/api/live/delete", JSON.stringify(data)).then(res => {
        if (String(res.data.code) === '1') {
          this.$message.success(res.data.msg)
        } else {
          this.$message.error(res.data.msg)
        }
      }).catch(err => {
        this.$message.error(err)
      })
      rows.splice(index, 1);
    },
    async stop(index, rows) {
      let data = {
        id: this.tableData[index].id
      }
      await this.$axios.post("/api/live/reset", JSON.stringify(data)).then(res => {
        if (String(res.data.code) === '1') {
          this.$message.success(res.data.msg)
        } else {
          this.$message.error(res.data.msg)
        }
      }).catch(err => {
        this.$message.error(err)
      })
      rows.splice(index, 1);
    },

    async getLive() {
      await this.$axios.get("/api/live/list").then(res => {
        if (String(res.data.code) === '1') {
          this.tableData = res.data.data || []
        }
      }).catch(err => {
        this.$message.error(err)
      })
    }
  }
}
</script>

<style>
.el-table .warning-row {
  background: oldlace;
}

.el-table .success-row {
  background: #f0f9eb;
}
</style>
