import Vue from 'vue'
import VueRouter from 'vue-router'
import IndexSystem from "@/components/IndexSystem";
import LoginSystem from "@/components/LoginSystem";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'index',
    component: LoginSystem
  },
  {
    path: '/live',
    name: 'live',
    component:IndexSystem
  }
]

const router = new VueRouter({
  routes
})

export default router
