<template>
  <el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="200px" class="demo-dynamic">
    <el-form-item
        v-for="(domain, index) in dynamicValidateForm.domains"
        :label="'直播间id' + (index+1)"
        :key="domain.key"
        :prop="'domains.' + index + '.value'"
        :rules="{
      required: true, message: '直播间', trigger: 'blur'
    }"
    >
      <el-row :gutter="10" style="margin: 0px;">
        <el-col :span="4">
          <el-input v-model="domain.value"></el-input>
        </el-col>
        <el-col :span="8">
          <el-input placeholder="备注" v-model="domain.country"></el-input>
        </el-col>
        <el-col :span="3">
          <el-button @click.prevent="removeDomain(domain)">删除</el-button>
        </el-col>
        <el-col :span="1">
          <el-button @click="domain.value=''">重置</el-button>
        </el-col>
      </el-row>


    </el-form-item>
    <el-form-item>
      <el-button v-show="dynamicValidateForm.domains.length!==0" type="primary"
                 @click="submitForm('dynamicValidateForm')">提交
      </el-button>
      <el-button @click="addDomain">新增直播间</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  name: "AddLinkCard",
  data() {
    return {
      dynamicValidateForm: {
        domains: []
      },
      prop: ['getLive']
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.dynamicValidateForm.domains.forEach(data => {
            this.add(data)
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    async add(data) {
      let param = {
        liveId: data.value,
        remark: data.country
      }
      await this.$axios.post("/api/live/add", JSON.stringify(param)).then(res => {
        if (String(res.data.code) === '1') {
          this.removeDomain(data)
          this.$message.success(res.data.msg)
          this.$emit('updateLive')
        } else {
          this.$message.error(res.data.msg)
        }
      }).catch(error => {
        alert(error)
      })
    },
    removeDomain(item) {
      var index = this.dynamicValidateForm.domains.indexOf(item)
      if (index !== -1) {
        this.dynamicValidateForm.domains.splice(index, 1)
      }
    },
    addDomain() {
      this.dynamicValidateForm.domains.push({
        value: '',
        country: ''
      });
    }
  }
}
</script>

