<template>
  <div class="login">
    <div class="bigBox">
      <div class="center-box">
        <div class="left"></div>
        <div class="box">
          <h1>Login</h1>
          <div class="inputBox">
            <input type="text" name="username" id="" required="required" v-model="form.user">
            <span class="placeholder">user name</span>
            <div class="error-placeholder" :style="status.name">
              <span>账号不存在</span>
              <div class="line"></div>
            </div>
          </div>
          <div class="inputBox">
            <input type="password" name="username" id="" required="required" v-model="form.password">
            <span class="placeholder">password</span>
            <div class="error-placeholder" :style="status.password">
              <span>密码错误!</span>
              <div class="line"></div>
            </div>
          </div>
          <div class="s" style="display: none">
            <label class="label-check">
              <input type="checkbox" id="remember">
              <span class="checkbox"></span>
            </label>
            <label class="text" for="remember">十天内免登录</label>
          </div>
          <div class="btn" @click="login" :style="status.loginWait">
            <span>Login</span>
          </div>
          <div class="loader" :style="status.loginWait=== 'visibility:hidden'? '':'visibility:hidden'"></div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "LoginSystem",
  data() {
    return {
      form: {
        user: '',
        password: ''
      },
      status: {
        name: 'visibility:hidden',
        password: 'visibility:hidden',
        loginWait: ''
      },

    }
  },
  mounted() {
    this.mouseEvent()

  },
  methods: {
    login() {
      // this.status.loginWait = 'visibility:hidden'
      let data = {
        userId: this.form.user,
        password: this.form.password
      }
      this.$axios.post("/api/manager/login", JSON.stringify(data)).then(res => {
        if (String(res.data.code) === '1') {
          this.$router.push({name: 'live'})
          this.$message.success("登录成功")
        }
      }).catch(err => {
        this.$message.error(err)
      })
    },

    mouseEvent() {
      for (let i = 0; i < 14; i++) {
        console.log(i)
      }
      let btn = document.querySelector(".btn")

      btn.addEventListener("mouseenter", (event) => {
        for (let i = 0; i < 14; i++) {
          console.log(i)
        }
        if (btn.style.getPropertyValue("--xPos") === "") {
          btn.style.setProperty("--xPos", event.clientX - btn.getBoundingClientRect().x + "px")
          if (event.clientY - btn.getBoundingClientRect().top < 20) {
            btn.style.setProperty("--yPos", event.clientY - btn.getBoundingClientRect().top - 20 + "px")
          } else {
            btn.style.setProperty("--yPos", event.clientY - btn.getBoundingClientRect().top + 20 + "px")
          }
        }

      })
      btn.addEventListener("mouseleave", () => {
        setTimeout(function () {
          if (btn.style.getPropertyValue("--xPos") !== "") {
            btn.style.setProperty("--xPos", "")
            btn.style.setProperty("--yPos", "")

          }
        }, 300)


      })
    }
  },
}


</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}

.login {
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(rgb(87, 119, 150), rgb(243, 221, 234), rgb(29, 44, 59));
  display: flex;
  justify-content: center;
}

.center-box {

  width: 1100px;
  height: 550px;
  margin-left: 5px;
  background-color: #30388100;
  position: relative;
  top: calc(50% - 275px);
  display: flex;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.8);
  border-radius: 15px;
  overflow: hidden;
  z-index: 1;
}

.bigBox {
  width: 1110px;
  height: 560px;
  background-color: #30388100;
  position: relative;
  top: calc(50% - 275px);
  display: flex;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.8);
  border-radius: 15px;
  overflow: hidden;
}

.bigBox::before {
  position: absolute;
  content: '';
  top: 50%;
  left: -50%;
  width: 1100px;
  height: 280px;
  background-image: linear-gradient(rgb(0, 255, 255), rgba(162, 230, 230, 0.151));
  transform-origin: top right;
  animation: animate 6s linear infinite;
  z-index: 1;
}

.bigBox::after {
  position: absolute;
  content: '';
  top: 50%;
  left: -50%;
  width: 1100px;
  height: 280px;
  background-image: linear-gradient(rgb(0, 255, 255), rgba(162, 230, 230, 0.151));
  transform-origin: top right;
  animation: animate 6s linear infinite;
  animation-delay: -3s;
  z-index: 0;
}

@keyframes animate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.left {
  background-color: aliceblue;
  background-image: url("../../public/images/login.png");
  height: 550px;
  width: 750px;
  background-size: cover;
  background-position: center;
  z-index: 2;
  border-radius: 15px 0 0 15px;
  box-shadow: 0 5px 29px rgba(82, 246, 255, 0.671);
}

.box {
  height: 550px;
  width: 350px;
  background-color: rgb(233, 254, 255);
  /* background-image: linear-gradient( rgb(87, 119, 150), rgb(243, 221, 234),rgb(29, 44, 59)); */
  display: flex;
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;
  border-radius: 0 15px 15px 0;
  z-index: 1;

}

.box h1 {
  font: 900 30px '';
  margin: 50px;
  user-select: none;
}

.box .inputBox {
  position: relative;
  width: 235px;
}

.box .inputBox input {
  font-size: 16px;
  width: 230px;
  outline: none;
  margin: 20px 0;
  border: none;
  border-radius: 5px;
  border-bottom: 1px solid rgb(145, 82, 216);
  padding: 10px;
  background-color: #ffffff00;
  transition: .5s;
}

.box .inputBox .placeholder {
  position: absolute;
  left: 14px;
  top: 36px;
  pointer-events: none;
  user-select: none;
  color: rgba(78, 15, 151, 0.582);
  text-transform: uppercase;
  transition: .5s;
}

.error-placeholder {
  position: relative;
  text-align: left;
  top: -1em;
  left: 1em;
  font-size: .8em;
  color: #f08080;
  user-select: none;
}

.box .inputBox .line {
  width: 2.5em;
  height: 1px;
  position: relative;
  left: 1.25em;
  /* border-bottom:  1px #f08080; */
  background-color: #ff0000;
}

.box .inputBox input:valid ~ .placeholder,
.box .inputBox input:focus ~ .placeholder {
  color: rgb(99, 221, 221);
  transform: translateX(5px) translateY(-28px);
  background-color: rgba(89, 58, 128, 0.74);
  font-size: .4em;
  padding: 3px;
}

.box .inputBox input:valid,
.box .inputBox input:focus {
  border: 1px solid rgb(145, 82, 216);
  background-color: rgb(232, 240, 254);
}

.btn {
  width: 200px;
  height: 35px;
  margin-top: 30px;
  border-radius: 1px;
  outline: none;
  padding: .3em;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  position: relative;
  text-align: center;
  color: rgba(22, 24, 15, 0.781);
  font-weight: 800;
  border: navajowhite;
  /*background-image: linear-gradient(rgb(147, 89, 212), rgb(117, 73, 168));*/
  background-image: linear-gradient(to right, rgb(147, 89, 212), rgb(117, 73, 168));
  overflow: hidden;
}

:root {
  --xPos: 0;
  --yPos: 0;
}

.btn::before {
  content: "";
  transition: width 0.3s, height 0.3s;
  position: absolute;
  width: 0;
  height: 0;
  left: var(--xPos);
  top: var(--yPos);
  border-radius: 50%;
  /* background-color:rgba(27, 224, 224, 0.76); */
  background: radial-gradient(rgba(255, 0, 0, .7), rgba(255, 255, 0, .7), rgba(0, 255, 255, .7), rgba(0, 0, 255, .7));
  transform: translate(-50%, -50%);
}

.btn:hover::before {
  content: "";
  position: absolute;
  width: 440px;
  height: 450px;


}

.btn span {
  line-height: 2em;
  position: relative;
  z-index: 0;
  user-select: none;
}


.loader {
  width: 40px;
  height: 40px;
  margin: auto;
  top: -90px;
  position: relative;
}

.loader:before {
  content: '';
  width: 48px;
  height: 5px;
  background: radial-gradient(rgba(0, 0, 255, .7), rgba(255, 255, 0, .7), rgba(0, 255, 255, .7), rgba(255, 0, 0, .7));
  position: absolute;
  top: 60px;
  left: 0;
  border-radius: 50%;
  animation: shadow324 0.5s linear infinite;
}

.loader:after {
  content: '';
  width: 100%;
  height: 100%;
  background: radial-gradient(rgba(109, 109, 223, 0.7), rgba(116, 236, 164, 0.7), rgba(0, 255, 255, .7), rgba(243, 104, 104, 0.7));
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 16px;
  animation: jump7456 0.5s linear infinite;
}

@keyframes jump7456 {
  15% {
    border-bottom-right-radius: 15px;
  }
  25% {
    transform: translateY(9px) rotate(22.5deg);
  }
  50% {
    transform: translateY(18px) scale(1, .9) rotate(45deg);
    border-bottom-right-radius: 40px;
  }
  75% {
    transform: translateY(9px) rotate(67.5deg);
  }
  100% {
    transform: translateY(0) rotate(90deg);
  }
}

@keyframes shadow324 {
  0%,
  100% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(1.2, 1);
  }
}

/* Variation of work by @mrhyddenn for Radios */

.label-check {
  display: block;
  width: 12px;
  height: 12px;
  cursor: pointer;

}

input[type='checkbox'] {
  position: absolute;
  transform: scale(0);

}

input[type='checkbox']:checked ~ .checkbox {
  transform: rotate(45deg);
  width: 10px;
  margin-left: 2px;
  border-color: #24c78e;
  border-width: 3px;
  border-top-color: transparent;
  border-left-color: transparent;
  border-radius: 0;
}

.checkbox {
  display: block;
  width: inherit;
  height: inherit;
  border: solid 3px #2a2a2ab7;
  border-radius: 6px;
  transition: all 0.375s;
}

.s {

  display: block;
  width: 350px;
  margin-left: 130px;
}

.s .text {
  cursor: pointer;
  color: lightcoral;
  position: relative;
  top: -14px;
  left: -100px;
  user-select: none;

}
</style>
