<template>
  <el-container>
    <el-header>
      tiktok
    </el-header>
    <el-container>

      <el-main>
        <add-link-card/>
        <live-view/>
      </el-main>

    </el-container>
  </el-container>
</template>

<script>

import AddLinkCard from "@/components/AddLinkCard";
import LiveView from "@/views/LiveView";

export default {
  name: "IndexSystem",
  data() {
    return {}
  },
  components: {
    LiveView,
    AddLinkCard

  },
  beforeCreate() {

      this.$axios.get("/api/live/list").then(res => {
      if (String(res.data.code) === '0' && res.data.msg === "未登录") {
        this.$router.push({name: 'index'})
        this.$message.error("未登录")
      }
    }).catch(err => {
      this.$message.error(err)
    })

  },
  methods: {}
}
</script>

<style scoped>
.el-header {
  background-color: #DCDFE6;
  color: rgba(11, 56, 40, 0.83);
  text-align: center;
  line-height: 60px;
  box-shadow: 0 2px 4px rgba(209, 45, 45, 0.12);
}

.el-aside {
  background-color: #e8e9ec;
  color: #333;
  text-align: center;
  height: calc(100vh - 60px);
  overflow: hidden;
  border-left: #888888 solid;
}

.el-main {
  padding: 1px;
  background-color: #ffffff;
  color: #333;
  text-align: center;
}

body > .el-container {
  margin-bottom: 40px;

}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}

.box {
  border: 1px solid;
  color: #888;
  box-shadow: 0 2px 10px #dc1a1a;
}

.el-main {
  padding: 0;
  height: calc(100vh - 60px);
}
</style>
